.Slider_container {
    width: 100%;
    height: 80vh;
}

.Slider_container-video {
    position: relative;
    height: 100%;
    width: 100%;
}

.Slider_Container-title {
    position: absolute;
    width: 40%;
    top: 30%;
    left: 20%;

    .Slider_title-item {
        width: 100%;
        color: #fff;
        background-color: transparent;
        border: none;
        font-size: 40px;
    }

    .Slider_title-description {
        width: 100%;
        color: #fff;
        background-color: transparent;
        border: none;
        font-size: 30px;
    }

    textarea::-webkit-scrollbar {
        width: 8px;
        /*  rng ca thanh cun */
    }
}

.Siler_container-btn {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    /* From Uiverse.io by andrew-demchenk0 */
    .button {
        position: relative;
        width: 150px;
        height: 40px;
        cursor: pointer;
        display: flex;
        align-items: center;
        border: 1px solid #34974d;
        background-color: #3aa856;
    }

    .button,
    .button__icon,
    .button__text {
        transition: all 0.3s;
    }

    .button .button__text {
        transform: translateX(10px);
        color: #fff;
        font-weight: 600;
    }

    .button .button__icon {
        position: absolute;
        transform: translateX(109px);
        height: 100%;
        width: 39px;
        background-color: #34974d;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .button .svg {
        width: 30px;
        stroke: #fff;
    }

    .button:hover {
        background: #34974d;
    }

    .button:hover .button__text {
        color: transparent;
    }

    .button:hover .button__icon {
        width: 148px;
        transform: translateX(0);
    }

    .button:active .button__icon {
        background-color: #2e8644;
    }

    .button:active {
        border: 1px solid #2e8644;
    }
}

.Slider_container-btn-save {
    // display: none;
    width: 99%;
    display: flex;
    justify-content: end;
    .Btn {
        width: 150px;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        border: none;
        border-radius: 4px;
        overflow: hidden;
        box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.164);
        cursor: pointer;
    }

    .text {
        width: 65%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        background-color: rgb(2, 153, 153);
    }

    .svgIcon {
        width: 35%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        background-color: teal;
    }

    .Btn:hover .text {
        background-color: rgb(0, 133, 133);
    }

    .Btn:hover .svgIcon {
        background-color: rgb(0, 105, 105);
    }
}