.btn_delete-row {
    .button {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: rgb(20, 20, 20);
        border: none;
        font-weight: 600;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.164);
        cursor: pointer;
        transition-duration: 0.3s;
        overflow: hidden;
        position: relative;
        gap: 2px;
        color: #fff;
    }

    .svgIcon {
        width: 12px;
        height: auto;
        transition-duration: 0.3s;
        background-color: #000 !important;
    }

    .svgIcon path {
        fill: white;
    }

    .button:hover {
        transition-duration: 0.3s;
        background-color: rgb(255, 69, 69);
        align-items: center;
        gap: 0;
    }

    .bin-top {
        transform-origin: bottom right;
    }

    .button:hover .bin-top {
        transition-duration: 0.5s;
        transform: rotate(160deg);
    }

}

.btn_update-row {
    .lock-button {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: rgb(20, 20, 20);
        border: none;
        font-weight: 600;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.164);
        cursor: pointer;
        transition-duration: 0.3s;
        overflow: hidden;
        position: relative;
        text-decoration: none !important;
        fill: white;
        color: white;
    }


    .lock-button:hover {
        border-radius: 50px;
        transition-duration: 0.3s;
        background-color: rgb(255, 69, 69);
        align-items: center;
    }

    .lock-button:hover .lock-svgIcon {
        width: 20px;
        transition-duration: 0.3s;
        // -webkit-transform: rotate(360deg);
    }

    .lock-button::before {
        display: none;
        color: white;
        transition-duration: 0.3s;
    }

    .lock-button:hover::before {
        display: block;
        opacity: 1;
        transform: translateY(0px);
        transition-duration: 0.3s;
    }

}

.radio-group {
    display: flex;
    flex-direction: column;
}

.radio-label {
    display: flex;
    align-items: center;
    padding: 0.5em;
    margin-bottom: 0.5em;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: background-color 0.2s, border-color 0.2s;
}

.radio-label:hover {
    background-color: #e6e6e6;
}

.radio-input {
    position: absolute;
    opacity: 0;
}

.radio-input:checked+.radio-label {
    background-color: #ffc3c3;
    border-color: #ff1111;
}

.radio-input:focus+.radio-label {
    outline: 2px solid #ff1111;
}

.radio-inner-circle {
    display: inline-block;
    width: 1em;
    height: 1em;
    border: 2px solid #888;
    border-radius: 50%;
    margin-right: 0.5em;
    transition: border-color 0.2s;
    position: relative;
}

.radio-label:hover .radio-inner-circle {
    border-color: #555;
}

.radio-input:checked+.radio-label .radio-inner-circle {
    border-color: #ff1111;
}

.radio-input:checked+.radio-label .radio-inner-circle::after {
    content: '';
    display: block;
    width: 0.5em;
    height: 0.5em;
    background-color: #ff1111;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.btn-detail {
    background-color: rgb(2, 183, 2);
